<template>
	<div class=" p16 " style="background-color: rgb(30, 32, 38);">
		<div class="f18 clof p5_0">
			<!-- <span><span class="">{{ areaList[active].text }}</span>交易所</span> -->
			<span><span class="">{{ info.areaName }}</span>交易所</span>
			<!-- <Poptip trigger="hover" placement="bottom-start">
				<span class="ml10 toggleBtn">切换</span>
				<template #title>
					<div class="sub_menu">品种</div>
				</template>
				<template #content>
					<div class="p10" style="background: #17171a;">
						<div class="gridMenus">
							<div v-for="(v,o) in areaList" :key="o" class="gridMenu" :class="{'active':active==o}" @click="active=o;search();">{{v.text}}</div>
						</div>
					</div>
				</template>
			</Poptip> -->
		</div>
		<div class="mt10">
			<div id="recommendDom" style="width:100%;height:400px;"></div>
		</div>
</div>
</template>

<script>
import { createChart } from "lightweight-charts";
import { generateCandlestickData } from './data.js'
export default {
	name: "",
	props: {
		info:{
            type:Object,
            default:()=>{}
        }
	},
	components: {},
	data() {
		return {
			bars:[],
            page:{
                "current": 1,
                "size": 200,
                loading:false,
                finish:false,
            },
			active:1,//区域。
            areaList:this.$store.state.app.areaList,//区域列表
		};
	},
	mounted() {
		//图表教程
		//https://tradingview.github.io/lightweight-charts/tutorials/customization/chart-colors
		this.getList(()=>{
			this.init();
		});
		// this.init();
	},
	methods: {
		search(){
            this.page.current=1;
            this.getList(()=>{
				this.init();
			});
        },
		//根据接口获取
        async getList(callback){
            if(this.page.loading){
                return;
            }
            let d={
                // area: this.areaList[this.active].type,
                area: this.info.area,
                "current": this.page.current,
                "size": this.page.size,
            };
            this.page.loading=true;
            let res = await this.$api.trading_view_page(d);
            let list = res.list || [];
            let arr = [];
            list.forEach(item => {
                arr.push({
                    time: this.$dayjs(new Date(item.date)).format('YYYY-MM-DD'),
                    close: item.close,
                    open: item.open,
                    high: item.highest,
                    low: item.lowest,
                    volume: item.totalAmount,
                })
				// time: "2018-10-19",
				// open: 180.34,
				// high: 180.99,
				// low: 178.57,
				// close: 179.85,
            })
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if(d.current==1){
                this.bars=arr;
            }else{
                this.bars=arr.concat(this.bars);
            }
            this.page.loading=false;
            // console.log(this.bars,'bars')
            if(callback){
                callback();
            }
        },
		//js文件引入
		init() {
			$('#recommendDom').html('');
			const chart = LightweightCharts.createChart(
				document.getElementById('recommendDom'),
				{
					layout: {
						// background: { color: "rgb(30, 32, 38)" },
						background: { color: "#16161a" },
						textColor: "#dddddd",
					},
					grid: {
						vertLines: { color: "#222531" },
						horzLines: { color: "#222531" },
					},
				}
			);
			

			// Setting the border color for the vertical axis
			chart.priceScale().applyOptions({
				// borderColor: "#71649C",
			});

			// Setting the border color for the horizontal axis
			chart.timeScale().applyOptions({
				// borderColor: "#71649C",
			});
			// 十字星样式
			// chart.applyOptions({
			// 	crosshair: {
			// 	// Change mode from default 'magnet' to 'normal'.
			// 	// Allows the crosshair to move freely without snapping to datapoints
			// 	mode: LightweightCharts.CrosshairMode.Normal,

			// 	// Vertical crosshair line (showing Date in Label)
			// 	vertLine: {
			// 		width: 8,
			// 		color: "#C3BCDB44",
			// 		style: LightweightCharts.LineStyle.Solid,
			// 		labelBackgroundColor: "#9B7DFF",
			// 	},

			// 	// Horizontal crosshair line (showing Price in Label)
			// 	horzLine: {
			// 		color: "#9B7DFF",
			// 		labelBackgroundColor: "#9B7DFF",
			// 	},
			// 	},
			// });
					
			
			// const candleStickData = generateCandlestickData();
			console.log(this.bars,'bars')
			const candleStickData = this.bars;


			const lineData = candleStickData.map((datapoint) => ({
				time: datapoint.time,
				value: (datapoint.close + datapoint.open) / 2,
			}));
            const areaSeries = chart.addAreaSeries({
				lastValueVisible: false, // hide the last value marker for this series
				crosshairMarkerVisible: false, // hide the crosshair marker for this series
				lineColor: "transparent", // hide the line
				topColor: "rgba(56, 33, 110,0.5)",
				bottomColor: "rgba(56, 33, 110, 0.05)",
			});
			areaSeries.setData(lineData);


			// Create the Main Series (Candlesticks)
			const mainSeries = chart.addCandlestickSeries();
			mainSeries.applyOptions({
				wickUpColor: '#e53935',
				upColor: '#e53935',//买入颜色
				wickDownColor: '#47a34b',
				downColor: '#47a34b',//卖出颜色
				borderVisible: false,
			});
			mainSeries.setData(candleStickData);


			window.addEventListener("resize", () => {
				// chart.resize(window.innerWidth, window.innerHeight);
				let box=$('#recommendDom');
				chart.resize(box.width(), box.height());
			});
		},
		init2() {
			// Function to generate a sample set of Candlestick datapoints


			// Create the Lightweight Chart within the container element
			// const chart = createChart(
			// 	document.getElementById("recommendDom"),
			// 	{
			// 		layout: {
			// 			background: { color: "#17171a" },
			// 			textColor: "#C3BCDB",
			// 		},
			// 		grid: {
			// 			vertLines: { color: "#333" },
			// 			horzLines: { color: "#333" },
			// 		},
			// 	}
			// );
			const chart = createChart(
				document.getElementById('recommendDom'),
				{
					layout: {
						background: { color: '#222' },
						textColor: '#DDD',
					},
					grid: {
						vertLines: { color: '#444' },
						horzLines: { color: '#444' },
					},
				}
			);
			// Setting the border color for the vertical axis
			//   chart.priceScale().applyOptions({
			//     borderColor: "#71649C",
			//   });

			// Setting the border color for the horizontal axis
			//   chart.timeScale().applyOptions({
			//     borderColor: "#71649C",
			//   });

			// Get the current users primary locale
			const currentLocale = window.navigator.languages[0];
			// Create a number format using Intl.NumberFormat
			const myPriceFormatter = Intl.NumberFormat(currentLocale, {
				style: "currency",
				currency: "EUR", // Currency for data points
			}).format;

			// Apply the custom priceFormatter to the chart
			chart.applyOptions({
				localization: {
					priceFormatter: myPriceFormatter,
				},
			});

			// Generate sample data to use within a candlestick series
			const candleStickData = generateCandlestickData();

			// Create the Main Series (Candlesticks)
			const mainSeries = chart.addCandlestickSeries();
			// Set the data for the Main Series
			mainSeries.setData(candleStickData);

			// Changing the Candlestick colors
			//   mainSeries.applyOptions({
			//     wickUpColor: "#e53935",
			//     upColor: "#e53935",
			//     wickDownColor: "#4caf50",
			//     downColor: "#4caf50",
			//     borderVisible: false,
			//   });
			// mainSeries.applyOptions({
			// 	wickUpColor: "#4caf50",
			// 	upColor: "#4caf50",
			// 	wickDownColor: "#e53935",
			// 	downColor: "#e53935",
			// 	borderVisible: false,
			// });
			chart.priceScale().applyOptions({
				borderColor: '#71649C',
			});

			// Setting the border color for the horizontal axis
			chart.timeScale().applyOptions({
				borderColor: '#71649C',
			});
			

			// Adding a window resize event handler to resize the chart when
			// the window size changes.
			// Note: for more advanced examples (when the chart doesn't fill the entire window)
			// you may need to use ResizeObserver -> https://developer.mozilla.org/en-US/docs/Web/API/ResizeObserver
			window.addEventListener("resize", () => {
				chart.resize(window.innerWidth, window.innerHeight);
			});
			const volumeSeries = chart.addHistogramSeries({
				priceFormat: {
					type: 'volume',
				},
				priceScaleId: '', // set as an overlay by setting a blank priceScaleId
				// set the positioning of the volume series
				scaleMargins: {
					top: 0.7, // highest point of the series will be 70% away from the top
					bottom: 0,
				},
			});
			// series.applyOptions({
			//     scaleMargins: {
			//         top: 0.7, // highest point of the series will be 70% away from the top
			//         bottom: 0, // lowest point will be at the very bottom.
			//     },
			// });
			// mainSeries.applyOptions({
			//     scaleMargins: {
			//         top: 0.1, // highest point of the series will be 10% away from the top
			//         bottom: 0.4, // lowest point will be 40% away from the bottom
			//     },
			// });
			// histogramSeries.setData([
			//     { time: '2018-10-19', value: 19103293.0, color: 'green' },
			//     { time: '2018-10-20', value: 20345000.0, color: 'red' },
			// ])
		}
	},
};
</script>

<style scoped lang='less'>
.defineTable {
	width: 100%;

	tbody td {
		padding: 10px 0;
	}
}
.toggleBtn{
    cursor: pointer;
    display: inline-block;
    padding:2px 8px;
    font-size: 12px;
    font-weight: normal;
    color: #3861fb;
    background: #2d2d2d;
    border-radius: 15px;
}
//气泡框
:deep(.ivu-poptip-inner){
    background: #2b2f38;
}
:deep(.ivu-poptip-title){
    background: #2b2f38;
}
:deep(.ivu-poptip-title:after){
    content:'';
    display: none;
}
:deep(.ivu-poptip-popper[x-placement^=bottom] .ivu-poptip-arrow:after){
    border-bottom-color: #2b2f38;
}
:deep(.ivu-poptip-popper[x-placement^=bottom] .ivu-poptip-arrow){
    border-bottom-color: rgb(43 47 56);
}
//通用二级分类菜单。样式一
.gridMenus{
    display: grid;
	grid-template-columns: 1fr 1fr 1fr 1fr;
	gap:5px;
}
.gridMenu{
    cursor: pointer;
	width: 60px;
    padding: 10px 5px;
    font-size:12px;
    border-radius: 8px;
	text-align: center;
}
.gridMenu:hover,
.gridMenu.active{
    white-space: nowrap;
    margin-right: 4px;
    background-color: rgba(56, 97, 251, 0.1);
    color: #3861fb;
}
</style>