<template>
<div class=" p16 " style="background-color: rgb(30, 32, 38);">
    <div class="f18 clof p5_0">昨日收盘价</div>
    <div class="mt10">
        <table class="defineTable dataList" cellspacing="0">
            <thead>
                <tr>
                    <th>#</th>
                    <!-- <th>品种代码</th> -->
                    <th>交易所</th>
                    <th>价格</th>
                    <th>涨跌幅</th>
                    <th>涨跌</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(v,o) in list" :key="o" :class="{'active':active==o}" @click="active=o;$emit('changeItem',v);" @dblclick="$router.push(`/trading?area=${v.area}`)">
                    <td>{{ o+1 }}</td>
                    <!-- <td>FQ25865</td> -->
                    <td>{{ v.areaName }}</td>
                    <td>{{ v.price }}</td>
                    <!-- <td :class="[$base.getColor(v.close,v.open)]">{{ (v.close-v.open)/v.open }}</td> -->
                    <td :class="[$base.getColor(v.close,v.open)]">
                        <span class="circleBox" :class="[$base.getColor(v.close,v.open)]">{{ v.increase*100 | toPercent(2,1) }}</span>
                    </td>
                    <!-- <td :class="[$base.getColor(v.close,v.open)]">{{ (v.close-v.open)/v.open*100 | toPercent(2,1) }}</td> -->
                    <td :class="[$base.getColor(v.close,v.open)]">{{ (v.close-v.open) | format(2,'',1)}}</td>
                </tr>
                <tr v-if="list.length==0">
                    <td colspan="5" class="center cloc">暂无数据</td>
                </tr>
                <!-- <tr v-for="(v,o) in 5" :key="o">
                    <td>{{ o+1 }}</td>
                    <td>FQ25865</td>
                    <td>全国交易所TESTL</td>
                    <td>50.5</td>
                    <td class="green">-5%</td>
                    <td class="green">-2.64</td>
                </tr> -->
            </tbody>
        </table>
    </div>
</div>
</template>

<script>
export default {
    name: '',
    props: {
    },
    components: {

    },
    data () {
        return {
            list:[],
            active:-1,
        }
    },
    mounted() {
        this.getList();
    },
    methods: {
        //列表
        async getList(){
            let res = await this.$api.market_all();
            this.list = res || [];
            if(this.list.length>0){
                this.$emit('changeItem',this.list[0]);
            }
        },
    },
}
</script>

